<template>
  <div class="main">
    <Headers title="商品详情" />
    <div class="content">
      <div class="imgs">
        <van-swipe @change="onChange" :show-indicators="false">
          <van-swipe-item v-for="(item,index) in arr" :key="index">
            <div
              class="imges"
              :style="{'background-image':`url(http://www.hyey.cn//${datas.imgPath})`}"
            ></div>
          </van-swipe-item>
        </van-swipe>
        <div class="number">{{ current + 1 }}/{{arr.length}}</div>
      </div>
      <div class="title">
        <div class="titles clear">
          <p class="p1">采购价：仅企业用户可看</p>
          <p class="p2">建议零售价￥{{datas.retailPrice}}</p>
        </div>
        <h1>
          {{datas.goodsName}}&nbsp;&nbsp;
          <i class="i">服</i>
        </h1>
      </div>
      <div style="
    width: 100%;
    height: 1px;
    background: #fff;">
        <p class="bor"></p>
      </div>
      <div>
        <ul>
          <li class="clear">
            <span class="left"  style="width: 2rem;">生产厂家</span>
            <span class="right"  style="width: 7.146rem;">{{datas.manufacturer}}</span>
          </li>
          <li class="clear">
            <span class="left"  style=":width: 2rem;">规格</span>
            <span class="right"  style="width: 7.146rem;">{{datas.spec}}</span>
          </li>
          <li class="clear">
            <span class="left"  style="width: 2rem;">批准文号</span>
            <span class="right" style="width: 7.146rem;">{{datas.license}}</span>
          </li>
          <li class="clear">
            <span class="left"  style="width: 2rem;">起批量</span>
            <span class="right" style="width: 7.146rem;">{{datas.amountOfPackage}}</span>
          </li>
          <li class="clear">
            <span class="left"  style="width: 2rem;">批号</span>
            <span class="right" style="width: 7.146rem;">--</span>
          </li>
          <li class="clear">
            <span class="left" style="width: 2rem;">有效期至</span>
            
            <span class="right" style="width: 7.146rem;">{{datas.validityDate.slice(0,10)}}</span>
          </li>
        </ul>

        <div>
          <div class="pei">
            <div class="div1">
              <p>配送方</p>
            </div>
            <div class="div2">
              <p style="font-size:0.426667rem;">{{datas.enterpriseName}}</p>
              <p class="prices">店铺起送金额：1000元</p>
            </div>
          </div>
        </div>
      </div>
      <div style="height: 1.333333rem;width:100%"></div>
    </div>

    <div class="end">
      <div class="car" @click="$router.push('/car')">
        <p>
          <img src="@/components/NavBar/images/car02.png" alt srcset />
        </p>
        <p>购物车</p>
      </div>
      <van-button type="info" class="btn">绑定企业查看更多信息</van-button>
    </div>
  </div>
</template>
<script>
import Headers from "@/components/Header";
export default {
  components: {
    Headers
  },
  data() {
    return {
      current: 0,
      arr:[1]
    };
  },
  computed: {
    datas() {
        if(sessionStorage.shop!==undefined){
            return JSON.parse(sessionStorage.shop)
        }else
      return this.$store.state.details;
      
    }
  },
  mounted() {
    if (screen.height >= document.querySelector(".main").offsetHeight) {
      document.querySelector(".main").style.bottom = 0;
    } else {
      document.querySelector(".main").style.bottom = "";
    }
    

    console.log(this.asss);
  },
  methods: {
    onChange(index) {
      this.current = index;
    }
  }
};
</script>
<style lang="less" scoped>
.main {
  background: #f4f4f5;
  position: absolute;
  left: 0;
  // bottom:0;
  top: 0;
  right: 0;
}
.content {
  margin-top: 1.1736rem;

  .imgs {
    height: 6.0264rem;
    width: 100%;
    text-align: center;
    position: relative;
    .imges {
      display: block;
      width: 100%;

      // height: 100%;
      height: 6.0264rem;
      background-position: center center;
      background-size: cover;
    }
  }
  .number {
    position: absolute;
    bottom: 0.453333rem;
    right: 0.426667rem;
    background: rgba(18, 20, 21, 0.3);
    border-radius: 0.293333rem;
    font-size: 0.32rem;
    color: #fff;
    font-weight: 500;
    padding: 0.053333rem 0.32rem;
  }
  .title {
    background-color: #ffffff;
    padding: 0.266667rem 0.426667rem;
    position: relative;
    .titles {
      line-height: 0.8rem;
      .p1 {
        color: #ff801a;
        float: left;
        font-size: 0.32rem;
      }
      .p2 {
        font-size: 0.32rem;
        color: #7d7e7f;
        margin-left: 0.32rem;
        float: right;
      }
    }
  }
  h1 {
    font-size: 0.426667rem;
    line-height: 1.6;
    .i {
      font-size: 0.32rem;
      font-style: normal;
      width: 0.426667rem;
      height: 0.426667rem;
      border-radius: 0.053333rem;
      display: inline-block;
      text-align: center;
      line-height: 0.426667rem;
      vertical-align: middle;
      background: rgba(67, 115, 234, 0.1);
      color: #4373ea;
    }
  }
}
ul {
  background-color: #fff;
  line-height: 1.41176471;
  font-size: 0.453333rem;
  overflow: hidden;
  position: relative;
  li {
    span {
      display: block;
      
      float: left;
    }
    color: #999;
    padding: 0.16rem 0.426667rem;
    .left {
      font-size: 0.373333rem;
      display: inline-block;
      color: #8c8e8f;
      width: 2rem;
    }
    .right {
      font-size: 0.373333rem;
      color: #2e3133;
      // font-weight: 700;
    }
  }
}
.pei {
  padding: 0.16rem 0.426667rem;
  margin-top: 0.32rem;
  //   width: 100%;
  display: flex;
  background: #fff;
  .div1 {
    flex: 21.9;
    color: #8c8e8f;
    font-size: 0.426667rem;
  }
  .div2 {
    flex: 78.1;
    color: #2e3133;
    .prices {
      font-size: 0.373333rem;
      color: #7d7e7f;
      margin-top: 0.213333rem;
    }
  }
}
.end {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.333333rem;
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;
  background-color: #ffffff;
  .car {
    width: 1.973333rem;
    text-align: center;
    height: 1.333333rem;
    color: #2e3133;
    background-color: #ffffff;
    img {
      display: block;
      width: 0.613333rem;
      height: 0.613333rem;
      margin: 0 auto;
      margin-top: 0.133333rem;
      border-top: 0;
    }
    p {
      margin-top: 0.1rem;
    }
  }
}
.btn {
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0.373333rem;
  padding-right: 0.373333rem;
  box-sizing: border-box;
  flex: 1;
  font-size: 0.48rem;
  text-align: center;
  text-decoration: none;
  color: #fff;
  line-height: 2.33333333;
  border-radius: 0.133333rem;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  overflow: hidden;
  height: 1.333333rem;
  border-radius: 0;
  font-size: 0.426667rem;
  background: linear-gradient(270deg, #4373ea, #439dea);
  box-shadow: 0 0.106667rem 0.16rem 0 rgba(67, 115, 234, 0.3);
}
.bor {
  border: 1px solid #e6e6e6;
  margin: 0 0.426667rem;
}
</style>


